.app__footer{
    position: relative;
    display: flex;
    flex-flow: row wrap;
    background-color: #222;
    opacity: .99;
    justify-content: center;
    padding: 5em;
    gap: 5em;

    &-links{
        flex: 1 1 300px;
        max-width: 450px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        gap: 1em;
        color: white;
        h2{
            font-size: 1.5em;
            font-weight: 400;
            @media screen and (max-width:768px) {
                text-align: center;
            }
        }
    }
    &-contactform{
        flex: 1 1 300px;
        max-width: 450px;
    }
    &::before{
        position: relative;
        width: 100%;
        border-top: .001em solid white;
        content: "";
    }
    @media screen and (max-width:1024px) {
        padding:3em;
        padding-top: 3em !important;
    }
    @media screen and (max-width:768px) {
        padding:1em; 
        gap: 3em;
    }
}