@import url('https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap');
.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:4em 5em;
    z-index: 2;
    background-color: rgba($color: #222, $alpha: .8);
    @media screen and (max-width:1024px) {
        padding:3em;
    }
    @media screen and (max-width:768px) {
        padding:1em; 
    }
}
.app__navbar-logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1em;
    img{
        height: 60px;
        width: 60px;
        border-radius: 50%;
        @media screen and (max-width:768px) {
            height: 35px;
            width: 35px;
        }
        @media screen and (min-width:2000px) {
            height: 100px;
            width: 100px;
        }
    }
    span{
        font-family: 'Nothing You Could Do', cursive;
        font-size: 3em;
        color: white;
        font-weight: 900;
        @media screen and (max-width:768px) {
            font-size: 2em;
        }
        @media screen and (min-width:2000px) {
            font-size: 5em;
        }
    }
}

.app__navbar-menu{
    z-index: 100;
    border-radius: 50%;
    opacity: 1;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    svg{
        width: 35px;
        height: 35px;
        color: white;
    }
    div{
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 1000;
        background-color: var(--bg-color);
        opacity: 1 !important;
        padding: 1em;
        width: 100vw;
        height: 100vh;
        display: flex;
        svg{
            height: 35px;
            width: 35px;
            color: white;
            position: absolute;
            right: 1em;
            top: 1em;
            @media screen and (min-width:768px) {
                right: 1em;
                top: 1em;
            }
        }
        ul{
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            gap: 1em;
            li{
                a{width: 50px;
                    height: 50px;
                    img{
                        width: 50px;
                        height: 50px;
                    }
                }
                &::marker{
                    content: "";
                }
            }
        }
    }
    @media screen and (min-width:768px) {
        display: none;
    }
}