.app__header{
    width: 100%;
    color: white;
    padding: 5em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background-color: #222;
    opacity: .8 !important;
   &-info{
    text-align: center;
    &-wrapper{
        h1{
            font-size: 3em;
            font-weight: 500;
            margin-bottom: 1em;
            
            @media screen and (max-width:768px) {
                font-size: 1.5em;
            }
            .changetext{
                color: red;
                font-weight: 700;
                border-right: 3px solid red;
                animation: blinkTextCursor 1s infinite ;
            }
        }
        h3{
            font-size: 1.5em;
            font-weight: 400;
            @media screen and (max-width:768px) {
                font-size: 1em;
            }
        }
    }
   }
    @media screen and (max-width:1024px) {
        padding: 5em 3em;
    }
    @media screen and (max-width:768px) {
        padding: 5em 1em;
    }
    
}

@keyframes blinkTextCursor{
    to{border-right-color: transparent;}
  }


