.app__project-filter{
    display: flex;
    background-color: rgba($color: #222, $alpha: .8);
    color: rgb(180, 180, 180);
    flex-direction: row;
    justify-content: space-between;
    align-content: center;
    flex-wrap: wrap;
    padding: 1em;
    div{
        font-size: 1em;
        transition: all .5s;
        cursor: pointer;
        &:hover{
            color: white;
            scale: 1.5;
        }
    }
    @media screen and (min-width:720px) {
        justify-content: center;
        padding: 3em;
        gap: 3em;
        div{
            font-size: 1.5em;
        }
    }
    @media screen and (min-width:1280px) {
        gap:5em;
        padding:5em;
    }
}

.app__project-portfolio{
    background-color: rgba($color: #222, $alpha: .99);
    padding: 1em;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: flex-start;
    gap: 2em 1em;
    overflow: hidden;
    @media screen and (min-width:720px) {
        padding: 3em;
        gap: 3em;
    }
    @media screen and (min-width:1280px) {
        gap: 5em;
        padding:5em;
    }
}

.app__project-item{
    display: flex;
    flex-direction: column;
    gap: 1em;
    flex: 1 1 300px;
    max-width: 500px;
    transition: all .5s;
    &-header{
        display: flex;
        gap: 1em;
        align-items: center;
        &-logo{
            width: 50px;
            height: 50px;
            border-radius: 50%;
            overflow: hidden;
            img{
                width: 50px;
                height: 50px;
            }
        }
        &-name{
            font-size: 1em;
            color: white;
        }
    }
    &-img{
        overflow: hidden;
        width: 100%;

        img{
            max-width: 100%;
        }
    }
    &-footer{
        display: flex;
        flex-direction: column;
        gap: 1em;
        h3{
            font-weight: 400;
            font-size: 1.5em;
            color: white;
        }
        &-tags{
            display: flex;
            flex-direction: row;
            font-size: .9em;
            gap: .5em;
            color: gray;
        }
    }
    cursor: pointer;
    &:hover{
        scale: 1.04;
        background-color: rgb(21, 21, 21);
    }
}