.app__footer_contact-form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 5em;
    h2{
        color: white;
        @media screen and (max-width:768px) {
            text-align: center;
        }
    }
    form{
        flex: 1 1 300px;
        max-width: 500px;
        align-self: center;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 3em;
        .field{
            position: relative;
            input,textarea{
                position: relative;
                padding: 1em;
                border-radius: 5px;
                border: 0;
                color: black;
                font-size: 1em;
                width: 100%;
                &:focus{
                    border: 2px solid red;
                }
                &:focus ~ .placeholder, &:valid ~ .placeholder {
                    top: -2em !important;
                    color: white;
                }
                
            }
            textarea{
                font-size: 1em;
            }
            .placeholder{
                position: absolute;
                top: 1em;
                left: 1em;
                width:fit-content;
                color: black;
                transition: all .3s;
            }
        }
        button{
            padding: .5em 1em;
            border-radius: 5px;
            border: none;
            font-weight: 500;
            font-size: 1em;
            background-color: white;
            width: fit-content;
            &:focus{
                scale: 1.2;
            }
            @media screen and (max-width:768px) {
                align-self: center;
            }
        }
    }
    @media screen and (max-width:768px) {
        gap: 3em;
    }
}