.app__social-links {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-height: 60px;
    li{
        margin: 0 .5em;
        cursor: pointer;
        flex-direction: column;

        a{
            color: yellow;
            flex-direction: column;
            transition: all 0.5s ease-in-out;
            display: block;
            height: 40px;
            width: 40px;
            border-radius: 50%;
            overflow: hidden;
            justify-content: center;
            align-items: center;
            img{
                height: 40px;
                width: 40px;
                border-radius: 50%;
            }
            &:hover{
                scale: 1.3;
            }
        }
    }

    @media screen and (max-width:768px) {
        display: none;
    }
}